<template>
  <v-container fluid>
    <v-row>
      <v-tabs
        v-if="subCategories.length > 0"
        optional
        align-with-title
        hide-slider
        dark
        background-color="indigo darken-3"
        height="100"
      >
        <v-tabs-slider color="teal lighten-3"></v-tabs-slider>
        <v-tab
          aria-multiline="true"
          class="white--text"
          v-for="subCategory in subCategories"
          :key="subCategory.id"
          :href="'/subcategories-list/' + subCategory.id"
          >{{ subCategory.name }}</v-tab
        >
      </v-tabs>
    </v-row>
    <v-row v-if="paginatedItems.length > 0">
      <v-col
        v-for="book in paginatedItems"
        :key="book.id"
        cols="12"
        sm="4"
        md="3"
        class="d-flex flex-column align-center"
      >
        <BookMedia :book="book" />
      </v-col>
    </v-row>
    <div class="text-center" v-if="paginatedItems.length > 0">
      <v-pagination
        v-model="page"
        :length="Math.ceil(this.booksWithUDC.length / this.perPage)"
        :total-visible="7"
      ></v-pagination>
    </div>
  </v-container>
</template>

<script>
import BookMedia from '@/components/BookMedia'
import { mapState } from 'vuex'
export default {
  name: 'SubCategoriesList',
  components: {
    BookMedia
  },
  data() {
    return {
      page: 1,
      perPage: 20
    }
  },
  created() {
    this.$store.dispatch('category/fetchCategories')
    this.$store.dispatch('book/fetchBooks')
  },
  computed: {
    ...mapState(['category', 'book', 'user']),
    subCategories() {
      return this.category.categories.filter(subcategory => {
        return subcategory.parent == this.$route.params.id
      })
    },
    universityBooks() {
      return this.book.books.filter(book => {
        return book.university === this.user.user.university_id
      })
    },
    booksWithUDC() {
      return this.universityBooks.filter(book => {
        return String(book.udc).startsWith(String(this.$route.params.id))
      })
    },
    paginatedItems() {
      return this.booksWithUDC.slice(
        (this.page - 1) * this.perPage,
        this.page * this.perPage
      )
    }
  }
}
</script>

<style scoped></style>
